<template>
  <v-card class="pb-1" height="165.78px">
    <v-card-title class="pt-1">THERMOSTAT <v-spacer></v-spacer>
      <div v-if="hasThermo">
      <Compressor v-for="(viewItem, key) in $displayOrder($view.thermostatCompressor)" :key="key" :view="viewItem"/>
      </div>
    </v-card-title> 
    <div class="hasThermostat" v-if="hasThermo">
    <v-container class="mt-n9">
      <v-layout row justify-space-between class="mb-n6 pt-2">
        <div class="box" :style="hover" v-for="(viewItem, key) in $displayOrder($view.thermostatSetTemp)" :key="key" >
          <v-flex class="pa-2"  >
            <BoxValue :view="viewItem" />
          </v-flex>
        </div>
      </v-layout>
    </v-container>
    <div class="d-flex "  :class="$vuetify.breakpoint.lg ? 'mt-n8' : 'mt-n13'">
      <v-spacer></v-spacer>
      <VueSvgGauge v-for="(viewItem, key) in $displayOrder($view.thermostatAvgTemp)" :key="key"
        class="mini-gauge"
        :style="gaugeSize"
        :start-angle="-110"
        :end-angle="110"
        :value="rawValue(viewItem._id)"
        :separator-step="25"
        :min="0"
        :max="100"
        :scale-interval="10"
        :inner-radius="75"
        :separator-thickness="4" 
        :base-color="'#838383'"
        :gauge-color="[{ offset: 68, color: '#0093D8' }, { offset: 80, color: '#f7941d' }, { offset: 100, color: '#d82b00' }]"
      >
        <v-container class="inner-text--2">
          <v-row>
            <v-col class="d-flex justify-center pa-0 ">
              <!-- <span :style="hover" v-for="(viewItem, key) in $displayOrder($view.thermostatAvgTemp)" :key="key" @click='showDialog(viewItem._id)'>{{viewItem.value}}</span> -->
              <span> <BoxValue :fontSize="'30px'" :hasName="false" :view="viewItem"/></span>
            </v-col>
          </v-row>
          <v-row class="pt-1">
            <v-col class="d-flex justify-center pa-0 mt-n2 overline">
              <div :style="hover">
              <!-- <span  v-for="(viewItem, key) in $displayOrder($view.thermostatMode)" :key="key" @click='showDialog(viewItem._id)'>{{viewItem.value}}</span> -->
              <span> <BoxValue :fontSize="'13px'" :hasName="false" :view="viewItem" v-for="(viewItem, key) in $displayOrder($view.thermostatMode)" :key="key"/></span>
              </div>
            </v-col>
          </v-row>
        </v-container>
      </VueSvgGauge>
      <v-spacer></v-spacer>
    </div>
    </div>
  </v-card>
</template>

<script>
import BoxValue from './BoxValue'
import Compressor from './Compressor'
import { VueSvgGauge } from 'vue-svg-gauge'

import eventBus from "../eventBus";

  export default {
      props:['cursor'],
      name:'thermostat',
      components:{
        BoxValue,
        Compressor,
        VueSvgGauge
      },
      data (){
        return {
          hasThermo:false,
        }
      },
      created(){
        if(this.$view.thermostatMode && this.$view.thermostatAvgTemp && this.$view.thermostatSetTemp){
            this.hasThermo = true
        } else {
            this.hasThermo = false
        }
      },
    computed:{
      hover(){
        return {
          '--cursor': this.cursor
        }
      },
      gaugeSize(){
        if(this.$vuetify.breakpoint.lg){
          return{'--gaugeSize': '156px'}
        } else {
          return {'--gaugeSize':'180px'}
        }
      }
    },
    methods:{
      showDialog(id){
        console.log("here",id)
        eventBus.$emit('details',id)
      },
      rawValue(id){
       // console.log(this.$view.thermostatAvgTemp[id].value)
        //if(!this.$view.thermostatAvgTemp[id].value === '--' || !this.$view.thermostatAvgTemp[id].value === '- - -'){
          
          if(Object.keys(this.$root.readingsTest).length > 0){
            return this.$root.readingsTest[id].rawValue
          } else {
            return 0
          }
         /* } else {
          return 50
        } */  
      },
      /* compStatus(viewItem, id){
        if(viewItem != '--' && viewItem != '- - -'){
          if(Object.keys(this.$root.readingsTest).length > 0){
            switch (this.$root.readingsTest[id].value) {
              case 'OFF':
                return 'rgba(255, 255, 255, 0.7)'
              case 'ON / Buffer Down State':
                return 'rgba(255, 255, 255, 0.7)'
              case 'OFF / Buffer On State':
                return 'rgba(146, 137, 11)'
              case 'ON':
                return 'rgba(11, 146, 61)'
              default:
                return 'rgba(255, 255, 255, 0.7)'
            }
          } else {
            return 'rgba(255, 255, 255, 0.7)'
          }
        }  else {
          return 'rgba(255, 255, 255, 0.7)'
        }
      }, */

    }
  }
</script>

<style scoped>
.boxtitle{
  width: 100%;
  margin-bottom: 5px;
  padding-top: 5px;
  font-size: 12px;
}
.boxvalue {
  font-size: 18px;
  font-weight: 700;
}
.tempvalue{
    font-size: 40px;
    margin-top: -40px;
}
.mini-gauge {
      max-width: var(--gaugeSize);
}
.inner-text--2 {
  padding-top: 55px;
  font-weight: bold;
  font-size: 30px;
}

.box {
    min-width:30%;
    cursor: var(--cursor);
    z-index: 1;
}
</style>