<template>
    <v-icon small :color="compressorsColor">{{compressorsIcon}}</v-icon> 
</template>

<script>
export default {
    props:['view',],
    data () {
            return {
                off:{icon:'', color:'rgba(255, 255, 255, 0.7)'},    // 1 - OFF
                onDown:{icon:'', color:'rgba(11, 146, 61)'},        // 19 - ON / Buffer Down State
                offUp:{icon:'', color:'rgba(146, 137, 11)'},        // 33 - OFF / Buffer On State
                on:{icon:'', color:'rgba(11, 146, 61)'},            // 49 - ON
                offline:{icon:'mdi-minus', color:'rgba(255, 255, 255, 0.7)'}
              
            }

    },
    created(){
        this.off.icon = this.$icon[`mdiNumeric${this.view.icon}`]
        this.onDown.icon = this.$icon[`mdiNumeric${this.view.icon}BoxOutline`]
        this.offUp.icon = this.$icon[`mdiNumeric${this.view.icon}Box`]
        this.on.icon = this.$icon[`mdiNumeric${this.view.icon}Box`]
    },
    computed:{
      compressorsColor(){
          if(this.view.value != '--' && this.view.value != '- - -'){
          if(Object.keys(this.$root.readingsTest).length > 0){
            //console.log(this.$root.readingsTest)
            switch (this.$root.readingsTest[this.view._id].value) {
              case 'OFF':
                return this.off.color
              case 'ON / Buffer Down State':
                return this.onDown.color
              case 'OFF / Buffer On State':
                return this.offUp.color
              case 'ON':
                return this.on.color
              default:
                return this.off.color
            }
          } else {
            return this.off.color
          }
        }  else {
          return this.offline.color
        }
      },
      compressorsIcon(){
          if(this.view.value != '--' && this.view.value != '- - -'){
          if(Object.keys(this.$root.readingsTest).length > 0){
            switch (this.$root.readingsTest[this.view._id].value) {
              case 'OFF':
                //console.log(this.off.icon)
                return this.off.icon
              case 'ON / Buffer Down State':
                return this.onDown.icon
              case 'OFF / Buffer On State':
                return this.offUp.icon
              case 'ON':
                return this.on.icon
              default:
                return this.off.icon
            }
          } else {
            return this.off.icon
          }
        }  else {
          return this.offline.icon
        }
      }
    }
}
</script>

